.about {
    display: flex;
    justify-content: center;
    height: 100vh;
    background: linear-gradient(90deg, rgba(2,0,36,1) 0%, rgba(6,0,23,1) 0%, rgba(0,1,25,1) 100%, rgba(255,0,249,1) 100%);
    border-bottom: 1px solid #350848;
}

.about_container {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    width: 70%;
    /*border: 1px solid white;*/
}

.about_title {
    color: #d5cece;
    font-size: 50px;
    margin-top: 70px;
}

.about_sub_title {
    margin-top: 15px;
    color: #d5cece;
    font-size: 17px;
    line-height: 30px;
    width: 60%;
}

.about_card_block {
    margin-top:80px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    /*align-items: center;*/
    /*flex-wrap: nowrap;*/
    /*justify-content: space-between;*/
    /*border: 1px solid white;*/
}
/* card */

.card_about_wrapper {
    /*width: 100%;*/
}

.card_about {
    width: 250px;
    background: linear-gradient(90deg, rgba(2,0,36,1) 0%, rgba(9,5,52,1) 0%, rgba(3,3,51,1) 100%, rgba(0,1,25,1) 100%);
    padding: 1px;
    border-radius: 20px;
    border: 2px solid #631584;
    box-shadow: 12px 16px 26px -3px rgba(221, 231, 239, 0.1);
}

.card_h {
    border-radius: 20px;
    padding: 5px 12px;
    min-height: 280px;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex-direction: column;
}

.card_r {
    width: 70px;
    height: 70px;
    object-fit: contain;
}

.card_m {
    color: white;
    font-size: 20px;
    font-weight: bold;
    text-align: center;
}


@media (min-width: 1900px) {
    .about {
        height: auto;
        padding-bottom: 100px;
    }

    .about_title {
        font-size: 65px;
    }

    .card_about {
        width: 350px;
        height: 450px;
    }
}

@media (max-width: 500px) {
    .about {
        height: auto;
        padding-bottom: 60px;
        width: 100%;
    }

    .about_container {
        width: 90%;
    }

    .about_card_block {
        flex-direction: column;
    }

    .about_sub_title {
        font-size: 17px;
        line-height: 30px;
        width: 100%;
    }

    .about_card_block {
        margin-top: 50px;
        align-items: center;
    }

    .about_title {
        font-size: 45px;
        margin-top: 50px;
    }

    .card_about_wrapper {
        margin-bottom: 25px;
        width: 100%;
    }

    .card_about {
        /*width: 350px;*/
        /*height: 450px;*/
        width: 100%;
    }
}
